import { createStyledAttrsMixin } from '@mekari/pixel-utils';
import { MpBox } from '@mekari/pixel-box';
import { MpText } from '@mekari/pixel-text';

const MpDivider = {
  name: 'MpDivider',
  mixins: [createStyledAttrsMixin('MpDivider')],
  props: {
    variant: {
      type: String,
      default: 'solid'
    },
    orientation: {
      type: String,
      default: 'horizontal'
    },
    label: {
      type: String
    }
  },
  computed: {
    componentStyles() {
      return {
        display: !this.label && 'flex',
        alignItems: !this.label && 'center',
        justifyContent: this.label ? 'space-between' : 'initial',
        position: 'relative',
        width: this.orientation === 'horizontal' ? '100%' : 'auto',
        borderTop: !this.label && this.orientation === 'horizontal' && `0.0625rem ${this.variant}`,
        borderInlineStart: !this.label && this.orientation === 'vertical' && `0.0625rem ${this.variant}`,
        borderColor: 'inherit',
        marginY: this.orientation === 'horizontal' && '2',
        marginX: this.orientation === 'vertical' && '2',
        opacity: 0.6
      };
    }

  },

  render(h) {
    return h(MpBox, {
      class: this.className,
      attrs: {
        'aria-orientation': this.orientation,
        ...this.computedAttrs
      }
    }, [this.label && this.orientation === 'horizontal' && h(MpText, {
      attrs: {
        display: 'flex',
        alignItems: 'center',
        _before: {
          content: '""',
          flex: 1,
          borderTop: `0.0625rem ${this.variant}`,
          borderColor: 'inherit',
          marginRight: '2',
          opacity: 0.6
        },
        _after: {
          content: '""',
          flex: 1,
          borderTop: `0.0625rem ${this.variant}`,
          borderColor: 'inherit',
          marginLeft: '2',
          opacity: 0.6
        }
      }
    }, this.label)]);
  }

};

export { MpDivider };
